___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  --sidebar-color: #9AD1D4;
  --main-color:#F9FAFB;
  --main-text-color:#1F2937;
}

body {
  font-family: var(--system-ui);
  display: grid;
  grid-template: 1fr 5fr 1fr / 100px 3fr;
  height: 100vh;
}
/* 1. Use a more-intuitive box-sizing model */
*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}
button,
input,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
}
header{
  grid-column: 1/3;
  text-align: center;
  padding-top: 1.5rem;
  background-color: var(--sidebar-color);
}
#projects{
  grid-row: 2/3;
  background-color: var(--sidebar-color);
}
#projects > div{
  display: flex;
  justify-content: space-between;
}
#projects > div p:hover{
  border-bottom: 1px solid #2563EB;
}
#todos{
  display: grid;
  grid-row: 2/3;
  grid-column: 2/3;
  justify-content: center;
  align-content: center;
  background-color: var(--main-color);
}
#todos > form{
  margin-bottom: 8px;
}
#todos > form > input{
  appearance: none;
  border:none;
  border-bottom: 1px solid black;
  color: var(--main-text-color);
}`, "",{"version":3,"sources":["webpack://./src/styles/style.css"],"names":[],"mappings":"AAAA;EACE,kIAAkI;EAClI,wBAAwB;EACxB,oBAAoB;EACpB,yBAAyB;AAC3B;;AAEA;EACE,6BAA6B;EAC7B,aAAa;EACb,sCAAsC;EACtC,aAAa;AACf;AACA,6CAA6C;AAC7C;EACE,sBAAsB;AACxB;;AAEA,6BAA6B;AAC7B;EACE,SAAS;AACX;;AAEA,iCAAiC;AACjC;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,kCAAkC;EAClC,gBAAgB;EAChB,8BAA8B;EAC9B,mCAAmC;AACrC;;AAEA,8BAA8B;AAC9B;EACE,cAAc;EACd,eAAe;AACjB;;AAEA,uCAAuC;AACvC;EACE,aAAa;AACf;;AAEA,4BAA4B;AAC5B;EACE,yBAAyB;AAC3B;;AAEA,6BAA6B;AAC7B;EACE,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;;AAEA;;CAEC;AACD;EACE,kBAAkB;AACpB;AACA;;;;EAIE,oBAAoB;EACpB,eAAe;AACjB;AACA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,mBAAmB;EACnB,sCAAsC;AACxC;AACA;EACE,aAAa;EACb,sCAAsC;AACxC;AACA;EACE,aAAa;EACb,8BAA8B;AAChC;AACA;EACE,gCAAgC;AAClC;AACA;EACE,aAAa;EACb,aAAa;EACb,gBAAgB;EAChB,uBAAuB;EACvB,qBAAqB;EACrB,mCAAmC;AACrC;AACA;EACE,kBAAkB;AACpB;AACA;EACE,gBAAgB;EAChB,WAAW;EACX,8BAA8B;EAC9B,6BAA6B;AAC/B","sourcesContent":[":root {\n  --system-ui: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  --sidebar-color: #9AD1D4;\n  --main-color:#F9FAFB;\n  --main-text-color:#1F2937;\n}\n\nbody {\n  font-family: var(--system-ui);\n  display: grid;\n  grid-template: 1fr 5fr 1fr / 100px 3fr;\n  height: 100vh;\n}\n/* 1. Use a more-intuitive box-sizing model */\n*, *::before, *::after {\n  box-sizing: border-box;\n}\n\n/* 2. Remove default margin */\n* {\n  margin: 0;\n}\n\n/* 3. Enable keyword animations */\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  /* 4. Add accessible line-height */\n  line-height: 1.5;\n  /* 5. Improve text rendering */\n  -webkit-font-smoothing: antialiased;\n}\n\n/* 6. Improve media defaults */\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\n\n/* 7. Inherit fonts for form controls */\ninput, button, textarea, select {\n  font: inherit;\n